.cart-outer-div {
    text-align: center;
}

.cart-total-price {
    position: relative;
    margin: max(1vw, 20px);
    color: orange;
    font-size: max(2vw, 16px);
}


.total-price-and-remove-all-div {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: max(1vw, 20px);
}

.remove-all {
    font-size: max(1.2vw, 12px);
    width: fit-content;
    background-color: orange;
    border-radius: max(1.3vw, 10px);
    padding: max(1vw, 5px);
    cursor: pointer;
}

.order-now {
    margin: 50px;
    width: max(15vw, 150px);
    height: max(5vw, 70px);
    cursor: pointer;
    font-weight: bolder;
    background-color: lightgreen;
    font-size: max(1.5vw, 16px);
}