.register-outer-div {
    position: absolute;
    display: flex;
    gap: 10px;
    right: 2vw;
    z-index: 2;
    bottom: max(2vw, 15px)
}

.register-buttons {
    padding: 2px;
    border-radius: 20px;
    width: max(7vw, 50px);
    font-size: max(1.5vw, 10px);
    cursor: pointer;
}

.register-buttons:hover {
    background-color: silver;
}


.register-login-form {
    position: absolute;
    z-index: 1;
    top: max(5vw, 50px);
    right: 0;
    background-color: white;
    padding: 20px;
    background-color: orange;
    width: max(17vw, 200px);
    height: fit-content;
    font-size: max(1.5vw, 16px);
}

.email-outer-div {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.password-outer-div {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-top: max(2vw, 10px);
}

.register-login-form input {
    height: max(1.5vw, 15px);
}


.login-button {
    width: max(6vw, 50px);
    font-size: max(1.5vw, 10px);
    padding: max(0.2vw, 5px);
    margin-top: 10px;
    cursor: pointer;
}

.log-out {
    padding: 2px;
    border-radius: 20px;
    width: max(7vw, 50px);
    font-size: max(1.5vw, 10px);
    cursor: pointer;
}

.welcome-username {
    font-size: max(2vw, 12px);
    color: orange;
    font-weight: bolder;
    padding: 5px;
    background-color: white;
    border-radius: 20px;
    margin-right: 20px;
}

.welcome-username-outer-div {
    position: absolute;
    display: flex;
    align-items: center;
    right: 0;
    bottom: 0;
}

