.order-outer-div {
    position: absolute;
    display: flex;
    align-items: center;
    flex-direction: column;
    top: 0;
    z-index: 2;
    width: 100%;
    height: 100%;
    background-color: rgba(192, 192, 192, 0.5)
}


.div-around-oops-p-button {
    position: sticky;
    top: 50%;
    color: red;
    font-weight: bold;
    font-size: max(2vw, 16px);
}

.close-order-button {
    background-color: red;
    font-weight: bolder;
    width: max(20vw, 100px);
    height: max(4vw, 70px);
    cursor: pointer;
    font-size: max(2vw, 16px);
    margin-top: 20px;
}