.registerForm-outer-div {
    display: flex;
    width: 100%;
    align-items: center;
    flex-direction: column;
    justify-content: space-evenly;
    height: max(30vw, 500px);
    border-radius: 20px;
    margin: 50px auto;
    color: orange;
}

.userName-outer-div {
    display: flex;
    margin: 0 auto;
    justify-content: space-between;
    width: 250px;
}

.lastName-outer-div {
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    width: 250px;
}

.registerForm-email-outer-div {
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    width: 250px;
}

.registerForm-password-outer-div {
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    width: 250px;
}

.registerForm-confirm-password-outer-div {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    width: 250px;
}

.register-free {
    font-weight: bolder;
    font-size: max(1.5vw, 16px);
}

.registerForm-submit-button {
    width: 200px;
    color: orange;
    font-weight: bolder;
    background-color: transparent;
    letter-spacing: 5px;
}

.registerForm-submit-button:hover {
    background-color: black;
    cursor: pointer;
}